/**
 * È™åËØÅÊä•ÂëäÁîüÊàêÂô®
 * 
 * Ë¥üË¥£ÁîüÊàêÂíåËæìÂá∫È™åËØÅÊä•Âëä
 * ÊîØÊåÅÂ§öÁßçÊ†ºÂºèÁöÑÊä•ÂëäËæìÂá∫
 * 
 * @author LDesign Team
 * @version 1.0.0
 */

import * as path from 'path'
import * as fs from 'fs-extra'
import type {
  IValidationReporter,
  ValidationReport,
  ValidationReportingConfig,
  ValidationResult,
  ValidationSummary
} from '../types/validation'
import { Logger } from '../utils/logger'

/**
 * È™åËØÅÊä•ÂëäÁîüÊàêÂô®ÂÆûÁé∞
 */
export class ValidationReporter implements IValidationReporter {
  /** Êó•ÂøóËÆ∞ÂΩïÂô® */
  private logger: Logger

  /**
   * ÊûÑÈÄ†ÂáΩÊï∞
   */
  constructor(options: {
    logger?: Logger
  } = {}) {
    this.logger = options.logger || new Logger({ level: 'info', prefix: 'ValidationReporter' })
  }

  /**
   * ÁîüÊàêÊä•Âëä
   */
  async generateReport(
    result: ValidationResult,
    _config: ValidationReportingConfig
  ): Promise<ValidationReport> {
    this.logger.info('ÁîüÊàêÈ™åËØÅÊä•Âëä...')

    const report: ValidationReport = {
      title: `ÊûÑÂª∫È™åËØÅÊä•Âëä - ${result.validationId}`,
      summary: this.generateSummary(result),
      details: {
        fileResults: [],
        formatResults: [],
        typeResults: [],
        styleResults: []
      },
      recommendations: this.generateRecommendations(result),
      generatedAt: Date.now(),
      version: '1.0.0'
    }

    return report
  }

  /**
   * ËæìÂá∫Êä•Âëä
   */
  async outputReport(
    report: ValidationReport,
    config: ValidationReportingConfig
  ): Promise<void> {
    this.logger.info(`ËæìÂá∫È™åËØÅÊä•Âëä (Ê†ºÂºè: ${config.format})`)

    switch (config.format) {
      case 'console':
        await this.outputConsoleReport(report, config)
        break
      case 'json':
        await this.outputJsonReport(report, config)
        break
      case 'html':
        await this.outputHtmlReport(report, config)
        break
      case 'markdown':
        await this.outputMarkdownReport(report, config)
        break
      default:
        await this.outputConsoleReport(report, config)
    }
  }

  /**
   * ÁîüÊàêÊëòË¶Å
   */
  private generateSummary(result: ValidationResult): ValidationSummary {
    return {
      status: result.success ? 'passed' : 'failed',
      totalFiles: result.stats.totalFiles,
      passedFiles: result.success ? result.stats.totalFiles : 0,
      failedFiles: result.success ? 0 : result.stats.totalFiles,
      totalTests: result.testResult.totalTests,
      passedTests: result.testResult.passedTests,
      failedTests: result.testResult.failedTests,
      duration: result.duration
    }
  }

  /**
   * ÁîüÊàêÂª∫ËÆÆ
   */
  private generateRecommendations(result: ValidationResult): any[] {
    const recommendations: any[] = []

    // Â¶ÇÊûúÈ™åËØÅÂ§±Ë¥•ÔºåÊ∑ªÂä†Âª∫ËÆÆ
    if (!result.success) {
      recommendations.push({
        type: 'error',
        title: 'È™åËØÅÂ§±Ë¥•',
        description: 'ÊâìÂåÖÂêéÁöÑ‰ª£Á†ÅÈ™åËØÅÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊûÑÂª∫ÈÖçÁΩÆÂíåÊµãËØïÁî®‰æã',
        solution: 'Ê£ÄÊü•ÊûÑÂª∫ËæìÂá∫ÂíåÊµãËØïÊó•ÂøóÔºå‰øÆÂ§çÁõ∏ÂÖ≥ÈóÆÈ¢ò',
        priority: 'high'
      })
    }

    // Â¶ÇÊûúÊúâÈîôËØØÔºåÊ∑ªÂä†ÂÖ∑‰ΩìÂª∫ËÆÆ
    if (result.errors.length > 0) {
      recommendations.push({
        type: 'error',
        title: 'ÂèëÁé∞ÈîôËØØ',
        description: `ÂèëÁé∞ ${result.errors.length} ‰∏™ÈîôËØØ`,
        solution: 'Êü•ÁúãËØ¶ÁªÜÈîôËØØ‰ø°ÊÅØÂπ∂ÈÄê‰∏Ä‰øÆÂ§ç',
        priority: 'high'
      })
    }

    // Â¶ÇÊûúÊúâË≠¶ÂëäÔºåÊ∑ªÂä†Âª∫ËÆÆ
    if (result.warnings.length > 0) {
      recommendations.push({
        type: 'warning',
        title: 'ÂèëÁé∞Ë≠¶Âëä',
        description: `ÂèëÁé∞ ${result.warnings.length} ‰∏™Ë≠¶Âëä`,
        solution: 'Êü•ÁúãË≠¶Âëä‰ø°ÊÅØÂπ∂ËÄÉËôë‰ºòÂåñ',
        priority: 'medium'
      })
    }

    // ÊÄßËÉΩÂª∫ËÆÆ
    if (result.duration > 60000) {
      recommendations.push({
        type: 'optimization',
        title: 'È™åËØÅËÄóÊó∂ËæÉÈïø',
        description: `È™åËØÅËÄóÊó∂ ${Math.round(result.duration / 1000)}sÔºåÂª∫ËÆÆ‰ºòÂåñ`,
        solution: 'ËÄÉËôëÂáèÂ∞ëÊµãËØïÁî®‰æãÊï∞ÈáèÊàñ‰ºòÂåñÊµãËØïÊÄßËÉΩ',
        priority: 'low'
      })
    }

    return recommendations
  }

  /**
   * ËæìÂá∫ÊéßÂà∂Âè∞Êä•Âëä
   */
  private async outputConsoleReport(
    report: ValidationReport,
    config: ValidationReportingConfig
  ): Promise<void> {
    const { summary } = report

    console.log('\n' + '='.repeat(60))
    console.log(`üìã ${report.title}`)
    console.log('='.repeat(60))

    // Áä∂ÊÄÅ
    const statusIcon = summary.status === 'passed' ? '‚úÖ' : '‚ùå'
    const statusText = summary.status === 'passed' ? 'ÈÄöËøá' : 'Â§±Ë¥•'
    console.log(`\n${statusIcon} È™åËØÅÁä∂ÊÄÅ: ${statusText}`)

    // ÁªüËÆ°‰ø°ÊÅØ
    console.log('\nüìä ÁªüËÆ°‰ø°ÊÅØ:')
    console.log(`   ÊÄªÊµãËØïÊï∞: ${summary.totalTests}`)
    console.log(`   ÈÄöËøáÊµãËØï: ${summary.passedTests}`)
    console.log(`   Â§±Ë¥•ÊµãËØï: ${summary.failedTests}`)
    console.log(`   È™åËØÅËÄóÊó∂: ${Math.round(summary.duration / 1000)}s`)

    // Âª∫ËÆÆ
    if (report.recommendations.length > 0) {
      console.log('\nüí° Âª∫ËÆÆ:')
      report.recommendations.forEach((rec, index) => {
        const icon = this.getRecommendationIcon(rec.type)
        console.log(`   ${index + 1}. ${icon} ${rec.title}`)
        if (config.verbose) {
          console.log(`      ${rec.description}`)
          if (rec.solution) {
            console.log(`      Ëß£ÂÜ≥ÊñπÊ°à: ${rec.solution}`)
          }
        }
      })
    }

    console.log('\n' + '='.repeat(60) + '\n')
  }

  /**
   * ËæìÂá∫ JSON Êä•Âëä
   */
  private async outputJsonReport(
    report: ValidationReport,
    config: ValidationReportingConfig
  ): Promise<void> {
    const outputPath = config.outputPath || 'validation-report.json'
    const reportJson = JSON.stringify(report, null, 2)

    await fs.ensureDir(path.dirname(outputPath))
    await fs.writeFile(outputPath, reportJson, 'utf8')

    this.logger.success(`JSON Êä•ÂëäÂ∑≤‰øùÂ≠òÂà∞: ${outputPath}`)
  }

  /**
   * ËæìÂá∫ HTML Êä•Âëä
   */
  private async outputHtmlReport(
    report: ValidationReport,
    config: ValidationReportingConfig
  ): Promise<void> {
    const outputPath = config.outputPath || 'validation-report.html'
    const html = this.generateHtmlReport(report)

    await fs.ensureDir(path.dirname(outputPath))
    await fs.writeFile(outputPath, html, 'utf8')

    this.logger.success(`HTML Êä•ÂëäÂ∑≤‰øùÂ≠òÂà∞: ${outputPath}`)
  }

  /**
   * ËæìÂá∫ Markdown Êä•Âëä
   */
  private async outputMarkdownReport(
    report: ValidationReport,
    config: ValidationReportingConfig
  ): Promise<void> {
    const outputPath = config.outputPath || 'validation-report.md'
    const markdown = this.generateMarkdownReport(report)

    await fs.ensureDir(path.dirname(outputPath))
    await fs.writeFile(outputPath, markdown, 'utf8')

    this.logger.success(`Markdown Êä•ÂëäÂ∑≤‰øùÂ≠òÂà∞: ${outputPath}`)
  }

  /**
   * ÁîüÊàê HTML Êä•Âëä
   */
  private generateHtmlReport(report: ValidationReport): string {
    const { summary } = report
    const statusClass = summary.status === 'passed' ? 'success' : 'error'

    return `
<!DOCTYPE html>
<html lang="zh-CN">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>${report.title}</title>
    <style>
        body { font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif; margin: 40px; }
        .header { border-bottom: 2px solid #eee; padding-bottom: 20px; margin-bottom: 30px; }
        .status { padding: 10px 20px; border-radius: 5px; margin: 20px 0; }
        .success { background-color: #d4edda; color: #155724; border: 1px solid #c3e6cb; }
        .error { background-color: #f8d7da; color: #721c24; border: 1px solid #f5c6cb; }
        .stats { display: grid; grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); gap: 20px; margin: 20px 0; }
        .stat-card { background: #f8f9fa; padding: 15px; border-radius: 5px; text-align: center; }
        .stat-number { font-size: 2em; font-weight: bold; color: #007bff; }
        .recommendations { margin-top: 30px; }
        .recommendation { margin: 10px 0; padding: 15px; border-left: 4px solid #007bff; background: #f8f9fa; }
    </style>
</head>
<body>
    <div class="header">
        <h1>${report.title}</h1>
        <p>ÁîüÊàêÊó∂Èó¥: ${new Date(report.generatedAt).toLocaleString()}</p>
    </div>

    <div class="status ${statusClass}">
        <h2>È™åËØÅÁä∂ÊÄÅ: ${summary.status === 'passed' ? '‚úÖ ÈÄöËøá' : '‚ùå Â§±Ë¥•'}</h2>
    </div>

    <div class="stats">
        <div class="stat-card">
            <div class="stat-number">${summary.totalTests}</div>
            <div>ÊÄªÊµãËØïÊï∞</div>
        </div>
        <div class="stat-card">
            <div class="stat-number">${summary.passedTests}</div>
            <div>ÈÄöËøáÊµãËØï</div>
        </div>
        <div class="stat-card">
            <div class="stat-number">${summary.failedTests}</div>
            <div>Â§±Ë¥•ÊµãËØï</div>
        </div>
        <div class="stat-card">
            <div class="stat-number">${Math.round(summary.duration / 1000)}s</div>
            <div>È™åËØÅËÄóÊó∂</div>
        </div>
    </div>

    ${report.recommendations.length > 0 ? `
    <div class="recommendations">
        <h2>üí° Âª∫ËÆÆ</h2>
        ${report.recommendations.map(rec => `
        <div class="recommendation">
            <h3>${this.getRecommendationIcon(rec.type)} ${rec.title}</h3>
            <p>${rec.description}</p>
            ${rec.solution ? `<p><strong>Ëß£ÂÜ≥ÊñπÊ°à:</strong> ${rec.solution}</p>` : ''}
        </div>
        `).join('')}
    </div>
    ` : ''}
</body>
</html>
    `.trim()
  }

  /**
   * ÁîüÊàê Markdown Êä•Âëä
   */
  private generateMarkdownReport(report: ValidationReport): string {
    const { summary } = report

    return `
# ${report.title}

**ÁîüÊàêÊó∂Èó¥:** ${new Date(report.generatedAt).toLocaleString()}

## È™åËØÅÁä∂ÊÄÅ

${summary.status === 'passed' ? '‚úÖ **ÈÄöËøá**' : '‚ùå **Â§±Ë¥•**'}

## ÁªüËÆ°‰ø°ÊÅØ

| È°πÁõÆ | Êï∞Èáè |
|------|------|
| ÊÄªÊµãËØïÊï∞ | ${summary.totalTests} |
| ÈÄöËøáÊµãËØï | ${summary.passedTests} |
| Â§±Ë¥•ÊµãËØï | ${summary.failedTests} |
| È™åËØÅËÄóÊó∂ | ${Math.round(summary.duration / 1000)}s |

${report.recommendations.length > 0 ? `
## üí° Âª∫ËÆÆ

${report.recommendations.map((rec, index) => `
### ${index + 1}. ${this.getRecommendationIcon(rec.type)} ${rec.title}

${rec.description}

${rec.solution ? `**Ëß£ÂÜ≥ÊñπÊ°à:** ${rec.solution}` : ''}
`).join('')}
` : ''}
    `.trim()
  }

  /**
   * Ëé∑ÂèñÂª∫ËÆÆÂõæÊ†á
   */
  private getRecommendationIcon(type: string): string {
    switch (type) {
      case 'error': return '‚ùå'
      case 'warning': return '‚ö†Ô∏è'
      case 'info': return '‚ÑπÔ∏è'
      case 'optimization': return '‚ö°'
      default: return 'üí°'
    }
  }
}
